import React from 'react';
import "../styles/menu.css"
import arrow from "../img/arrow.png"
import plus from "../img/plus.png"

export default class Menu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {menu: 'hide', projects: [1, 2]}
    }

    onShow = () => {
        this.setState({menu: this.state.menu === "hide" ? "show" : "hide"})
        document.getElementById('arrow').classList.toggle('rotate')
        document.getElementById('block-account').className = this.state.menu === "show" ? "menu-block" : "menu-block menu-block-a"
        return false
    }

    onAdd = () => {
        const projects = this.state.projects
        const last = projects[projects.length - 1]
        projects.push(last + 1)
        this.setState({projects: projects})
    }

    render() {
        return (
            <div className="menu">

                <div className="menu-block menu-block-projects">
                    <a className="menu-link" onClick={this.onShow}><img id="arrow" className="img-arrow" alt="" src={arrow} width="25px" height="13px"></img>Projects</a>    
                </div> 

                <div id="projects" className={this.state.menu}>
                    {
                        this.state.projects.map(project => {
                            return <li key={project} style={project % 2 === 0 ? {color: "#FB8644"} : {color: "#4A9DFF"}}>
                                <a className="menu-link link-project" href="!#">{"Project " + project}</a>
                                </li>
                            })
                    }
                    <div className="block-add">
                        <a className="link-add" href="!#" onClick={this.onAdd}>
                            <img className="img-plus" src={plus} alt="" width="20px" height="20px"></img>
                            Add new
                        </a>
                    </div>
                </div>

                <div id="block-account" className="menu-block">
                    <a className="menu-link" href="!#">Account</a>
                </div>

                <div className="menu-block">
                    <a className="menu-link" href="!#">Support</a>
                </div>
            </div>
        )
    }
}